/* Styles for the article list container */
.article-list {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

/* Styles for individual articles */
.article {
  display: flex;
  align-items: flex-start;
  padding: 10px;
  border-bottom: 1px solid #ccc;
}

/* Styles for article images */
.article img {
  width: 100px;
  height: 100px;
  object-fit: cover;
  margin-right: 10px;
}

/* Styles for article details */
.article-details {
  flex: 1;
}

/* Styles for article titles */
.article h3 {
  margin: 0;
  font-size: 18px;
  font-weight: bold;
  color: #fff;
}

.grid h2 {
  margin: 0;
  font-size: 24px;
  font-weight: bold;
  text-align: center; /* Center the title */
  justify-content: center;
  color: #fff;
}

.article h3:hover {
  color: #ccc;
  cursor: pointer;
}

/* Styles for article summaries */
.article p {
  margin: 0;
  font-size: 14px;
  color: #fff;
}

/* Styles for article tags */
.article-tags {
  display: flex;
  justify-content: flex-end;
  flex-wrap: wrap;
}

/* Styles for individual tags */
.article-tag {
  display: inline-block;
  padding: 5px 10px;
  margin-left: 5px;
  margin-top: 5px;
  color: #fff;
  font-size: 12px;
  border-radius: 20px;
}

.article-tag:hover {
  cursor: pointer;
  scale: 1.3;
}

html {
  background-image: linear-gradient(to bottom right, #2a1344, #29005c);
}

/* Styles for the full-page article container */
.full-page-article {
  text-align: center; /* Center the title */
  justify-content: center;
  color: #fff;
  max-width: "60vw";
  margin: 0 auto;
  padding: 20px;
  font-family: Arial, sans-serif;
}

/* Styles for the article title */
.article-title {
  font-size: 28px;
  font-weight: bold;
  margin-bottom: 20px;
}

/* Styles for the article section */
.article-section {
  margin-bottom: 30px;
}

/* Styles for the section title */
.section-title {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 10px;
}

/* Styles for the section image */
.section-image {
  width: 200px;
  height: auto;
  margin-bottom: 10px;
  border-radius: 10px;
}

/* Styles for the section content */
.section-content {
  font-size: 20px;
  line-height: 1.5;
  color: #333;
}
